* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.button-container {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  top: 10em;
}

.start-button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #afac4c;
  border: 3px solid #200064;
  border-radius: 5px;
  margin-right: 10px;
  padding: 15px 32px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.stop-button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #4caf50;
  border: 3px solid #006400;
  border-radius: 5px;
  padding: 15px 32px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.timer {
  justify-content: start;
  align-items: center;
  margin-top: 15px;
  display: flex;
}

.field {
  width: 80px;
  border: 3px solid #00f;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  display: flex;
}

.field:not(:last-child) {
  margin-right: 15px;
}

.value {
  font-weight: bold;
}

.timer-start {
  width: 65px;
  height: 40px;
  cursor: pointer;
  color: #00f;
  background-color: #ff0;
  border: 3px solid #00f;
  margin-left: 15px;
}

.input-data {
  height: 40px;
  width: 280px;
  border: 3px solid #00f;
  font-size: 18px;
}

.form-container {
  width: 609px;
  border: 3px dashed #4caf50;
  margin-left: 20px;
  padding: 17px;
  display: flex;
}

.form {
  flex-flow: wrap;
  justify-content: center;
  display: flex;
}

.form-label {
  flex-direction: column;
  align-items: center;
  margin-right: 15px;
  font-weight: bold;
  display: flex;
}

.form-label:nth-child(4n-1) {
  margin-right: 0;
}

.form-button {
  width: 200px;
  height: 60px;
  cursor: pointer;
  color: #fff;
  background-color: #4caf50;
  border: 3px solid #006400;
  border-radius: 5px;
  margin-top: 15px;
  font-size: 20px;
}

.form-input {
  height: 30px;
  border: 3px solid #006400;
  margin-top: 5px;
}

