* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 10em;
}

.start-button {
  background-color: #afac4c;
  border: 3px solid rgb(32, 0, 100);
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-right: 10px;
  cursor: pointer;
  border-radius: 5px;
}

.stop-button {
  background-color: #4caf50;
  border: 3px solid darkgreen;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}

.timer {
  display: flex;
  justify-content: start;
  align-items: center;
  margin-top: 15px;
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: blue 3px solid;
  padding: 10px;
  width: 80px;
}

.field:not(:last-child) {
  margin-right: 15px;
}

.value {
  font-weight: bold;
}

.timer-start {
  margin-left: 15px;
  width: 65px;
  height: 40px;
  cursor: pointer;
  background-color: yellow;
  border: 3px solid blue;
  color: blue;
}

.input-data {
  border: blue 3px solid;
  height: 40px;
  width: 280px;
  font-size: 18px;
}

.form-container {
  display: flex;
  width: 609px;
  padding: 17px;
  border: #4caf50 3px dashed;
  margin-left: 20px;
}

.form {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.form-label {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: bold;
  margin-right: 15px;
}

.form-label:nth-child(4n-1) {
  margin-right: 0px;
}

.form-button {
  margin-top: 15px;
  width: 200px;
  height: 60px;
  background-color: #4caf50;
  border: 3px solid darkgreen;
  cursor: pointer;
  color: white;
  border-radius: 5px;
  font-size: 20px;
}

.form-input {
  margin-top: 5px;
  height: 30px;
  border: darkgreen 3px solid;
}
